:root {
  --primary-color: #00acdc;
}

body {
  background-color: #e0e0e0;
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  padding: 0 50px;
  display: flex;
}

.logo {
  display: flex;
}

.logo__img {
  width: 40px;
  height: 40px;
  text-decoration: none;
}

.nav {
  align-items: center;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  margin-left: 20px;
  position: relative;
}

.nav_item:first-child {
  margin-left: 0;
}

.nav__link {
  white-space: nowrap;
  color: #000;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-size: 12px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link.is-active:after {
  content: "";
  background-color: var(--primary-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin: 0;
  position: absolute;
  bottom: 0;
}

.nav__link:hover, .nav__link.is-active {
  color: var(--primary-color);
}
